.footer {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  bottom: 0;
  margin-top: 150px;
  margin-bottom: -10px;
  max-width: 1440px;
  height: 220px;
  background-color: black;
  justify-content: center;
  padding: 0;
}
.infofooter {
  display: flex;
  margin-top: 60px;
  justify-content: center;
  text-align: center;
  max-width: 400px;
  flex-direction: column;
  font-weight: 500;
  font-size: 24px;
  color: white;
}

.infofooter img {
  max-width: 150px;
  margin-left: auto;
  margin-right: auto;
  filter: brightness(100);
  color: white;
}
